<template>
  <v-container
    v-if="logado === 'true' ? true : false"
    id="user-profile-view"
    fluid
    tag="section"
  >
    <v-overlay :value="showUpdate">
      <v-alert
        icon="mdi-check"
        prominent
        shaped
        type="success"
        transition="scale-transition"
        class="position-fixed"
      >
        Perfil atualizado com sucesso!
      </v-alert>
    </v-overlay>
    <v-overlay :value="showError">
      <v-alert
        icon="mdi-alert"
        prominent
        shaped
        type="error"
        transition="scale-transition"
        class="position-fixed"
      >
        Algo de errado não está certo.
      </v-alert>
    </v-overlay>

    <v-row justify="center">
      <v-col
        cols="12"
        md="8"
      >
        <v-card class="mt-7 mb-10 mb-md-0">
          <v-row>
            <v-col
              cols="6"
              md="2"
            >
              <v-hover v-slot="{ hover }">
                <label for="file-input">
                  <v-img
                    class="rounded-circle mt-n16 ms-6 profile-pic"
                    align="center"
                    width="125"
                    :aspect-ratio="1/1"
                    :src="profilePic"
                    style="cursor: pointer"
                  >
                    <v-fab-transition>
                      <v-icon
                        v-if="hover"
                        class="fill-height"
                        large
                        dark
                      >
                        mdi-camera
                      </v-icon>
                    </v-fab-transition>
                  </v-img>
                </label>
              </v-hover>
              <input
                v-show="false"
                id="file-input"
                type="file"
                accept=".pdf,.jpg,.jpeg,.png"
                show-size
                @change="onFileChange"
              >
              <v-btn
                fab
                small
                :href="profilePic"
                download="Foto de perfil"
              >
                <v-icon
                  dark
                  color="green darken-2"
                >
                  mdi-download
                </v-icon>
              </v-btn>
            </v-col>
          </v-row>

          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk1"
                    color="blue"
                    label="1"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk2"
                    color="blue"
                    label="2"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk3"
                    color="blue"
                    label="3"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk4"
                    color="blue"
                    label="4"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk5"
                    color="blue"
                    label="5"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk6"
                    color="blue"
                    label="6"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk7"
                    color="blue"
                    label="7"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk8"
                    color="blue"
                    label="8"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk9"
                    color="blue"
                    label="9"
                    counter
                    outlined
                  />
                </v-col>
                <v-col cols="4">
                  <v-textarea
                    v-model="chunk10"
                    color="blue"
                    label="10"
                    counter
                    outlined
                  />
                </v-col>
              </v-row>

              <v-col
                cols="12"
                class="text-right"
              >
                <v-btn
                  color="primary"
                  min-width="150"
                  @click="separateInChunks"
                >
                  Dividir
                </v-btn>
                <v-btn
                  color="primary"
                  min-width="150"
                  @click="editProfile"
                >
                  Atualizar Perfil
                </v-btn>
              </v-col>
            </v-container>
          </v-form>
        </v-card>
      </v-col>
