</template>

<script>
  import axios from 'axios'

  export default {
    name: 'ChunksTest',

    data: () => ({
      showUpdate: false,
      showProfileUpdate: false,
      showError: false,
      originalImg: '',
      profilePic: window.localStorage.img,
      logado: window.localStorage.logado,
      chunk1: '',
      chunk2: '',
      chunk3: '',
      chunk4: '',
      chunk5: '',
      chunk6: '',
      chunk7: '',
      chunk8: '',
      chunk9: '',
      chunk10: '',
      icon: [
        { text: 'mdi-camera' },
      ],
      items: {
        sobre: window.localStorage.sobre,
      },
    }),

    watch: {
      items: {
        handler () {
          window.localStorage.sobre = this.items.sobre
        },
        deep: true,
      },
    },

    methods: {
      set (key) {
        try {
          localStorage.setItem(key, this.profilePic)
        } catch (e) {
          console.log(`Falha no armazenamento: ${e}`)
        }
      },
      onFileChange (e) {
        var files = e.target.files || e.dataTransfer.files
        if (!files.length) {
          return
        }
        this.createImage(files[0])
      },
      createImage (file) {
        var reader = new FileReader()

        reader.onload = (e) => {
          this.profilePic = e.target.result
          this.set('img')
        }
        reader.readAsDataURL(file)
      },
      separateInChunks () {
        const [chunk1, chunk2, chunk3, chunk4, chunk5, chunk6, chunk7, chunk8, chunk9, chunk10] = JSON.stringify(this.stringChop(this.profilePic,50000)).replace('["', '').replace('"]', '').split(',')
        console.log(this.profilePic.length)
        if (chunk1 != undefined) this.chunk1 = chunk1.replace('"', '').replace('"', '').concat(',')
        if (chunk2 != undefined) this.chunk2 = chunk2.replace('"', '').replace('"', '')
        if (chunk3 != undefined) this.chunk3 = chunk3.replace('"', '').replace('"', '')
        if (chunk4 != undefined) this.chunk4 = chunk4.replace('"', '').replace('"', '')
        if (chunk5 != undefined) this.chunk5 = chunk5.replace('"', '').replace('"', '')
        if (chunk6 != undefined) this.chunk6 = chunk6.replace('"', '').replace('"', '')
        if (chunk7 != undefined) this.chunk7 = chunk7.replace('"', '').replace('"', '')
        if (chunk8 != undefined) this.chunk8 = chunk8.replace('"', '').replace('"', '')
        if (chunk9 != undefined) this.chunk9 = chunk9.replace('"', '').replace('"', '')
        if (chunk10 != undefined) this.chunk10 = chunk10.replace('"', '').replace('"', '')
        this.downloadableFile = chunk1.concat(chunk2).concat(chunk3).concat(chunk4).concat(chunk5).concat(chunk6).concat(chunk7).concat(chunk8).concat(chunk9).concat(chunk10)
      },
      stringChop (str, size) {
        if (str == null) return []
        str = String(str)
        size = ~~size
        return size > 0 ? str.match(new RegExp('.{1,' + size + '}', 'g')) : [str]
      },

      editProfile () {
        axios.put(`https://sheetdb.io/api/v1/1q4dfgpu4na0b/ID/1`, {
          data: {
            chunk1: `${this.chunk1}`,
            chunk2: `${this.chunk2}`,
            chunk3: `${this.chunk3}`,
            chunk4: `${this.chunk4}`,
            chunk5: `${this.chunk5}`,
            chunk6: `${this.chunk6}`,
            chunk7: `${this.chunk7}`,
            chunk8: `${this.chunk8}`,
            chunk9: `${this.chunk9}`,
            chunk10: `${this.chunk10}`,
          },
        })
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 3500)
            } else {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
              }, 3500)
            }
          })
      },
    },
  }
</script>

<style scoped>
  .profile-pic:hover {
    opacity: 0.6;
  }
</style>
